@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Oswald:wght@400;600&display=swap);
html {
  scroll-behavior: smooth;

  --main-black-color: #2c2c2c;
  /* --main-white-color: #eeeeee; */
  --main-white-color: #FFFCF9;
  --main-red-color: #EE6352;
  --main-blue-color: #08B2E3;
  --second-white-color: #EFE9F4;
  --main-green-color: #57A773;
  --main-purple-color: #484D6D;

  --header-font: 'Oswald', sans-serif;
  --body-font: 'Open Sans', sans-serif;
  /* --body-font-bold: 'Open Sans', bold; */
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  /* font-family: 'Open Sans', sans-serif;
  font-family: 'Oswald', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background: #FFFCF9;
  background: var(--main-white-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  opacity: 1;

  -webkit-animation: fadeIn 2s linear;
  animation: fadeIn 2s linear;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

a {
  color: var(--main-black-color);
  text-decoration: none;
}

/* Fix weird bar */
svg {
  display: block;
}

img {
  animation: lower 0.2s linear; /*other browsers*/
  -webkit-animation: lower 0.2s linear; /*webkit browsers*/
  -moz-animation: lower 0.2s linear; /*firefox*/
  -o-animation: lower 0.2s linear; /*opera*/
}

img:hover {
  transform: translateY(-10px);
  -webkit-transform: translateY(-10px);
  -moz-transform: translateY(-10px);
  -o-transform: translateY(-10px);


  animation: raise 0.2s linear; /*other browsers*/
  -webkit-animation: raise 0.2s linear; /*webkit browsers*/
  -moz-animation: raise 0.2s linear; /*firefox*/
  -o-animation: raise 0.2s linear; /*opera*/
}

/* Animations */

/* Fade In */
@-webkit-keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}
@keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}

/* Image Bounce */
@-webkit-keyframes raise {
  0% {
    -webkit-transform: translateY(0px);

  }
  100% {
    -webkit-transform: translateY(-10px);
  }
}
@keyframes raise {
  0% {
    transform: translateY(0px);

  }
  100% {
    transform: translateY(-10px);
  }
}

/* Image Lower */
@-webkit-keyframes lower {
  0% {
    -webkit-transform: translateY(-10px);

  }
  100% {
    -webkit-transform: translateY(0px);
  }
}
@keyframes lower {
  0% {
    transform: translateY(-10px);

  }
  100% {
    transform: translateY(0px);
  }
}
#nav-div {
    position: fixed;
    right: 0px;
    top: 0px;
    z-index: 999;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.nav-item {
    text-align: right;
    margin: 10px;
    font-family: var(--header-font);
    letter-spacing: 0.2rem;

    -webkit-animation: offHover 0.4s linear;
    animation: offHover 0.4s linear;
}

.nav-item:hover {
    letter-spacing: 0.3rem;

    -webkit-animation: onHover 0.2s linear;
    animation: onHover 0.2s linear;
}

/* Animations */

@-webkit-keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}
@keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}

@-webkit-keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}
@keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}
