.App {
  text-align: center;
  opacity: 1;

  -webkit-animation: fadeIn 2s linear;
  -moz-animation: fadeIn 2s linear;
  -o-animation: fadeIn 2s linear;
  animation: fadeIn 2s linear;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

a {
  color: var(--main-black-color);
  text-decoration: none;
}

/* Fix weird bar */
svg {
  display: block;
}

img {
  animation: lower 0.2s linear; /*other browsers*/
  -webkit-animation: lower 0.2s linear; /*webkit browsers*/
  -moz-animation: lower 0.2s linear; /*firefox*/
  -o-animation: lower 0.2s linear; /*opera*/
}

img:hover {
  transform: translateY(-10px);
  -webkit-transform: translateY(-10px);
  -moz-transform: translateY(-10px);
  -o-transform: translateY(-10px);


  animation: raise 0.2s linear; /*other browsers*/
  -webkit-animation: raise 0.2s linear; /*webkit browsers*/
  -moz-animation: raise 0.2s linear; /*firefox*/
  -o-animation: raise 0.2s linear; /*opera*/
}

/* Animations */

/* Fade In */
@-webkit-keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}
@-moz-keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}
@-o-keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}
@keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}

/* Image Bounce */
@-webkit-keyframes raise {
  0% {
    -webkit-transform: translateY(0px);

  }
  100% {
    -webkit-transform: translateY(-10px);
  }
}
@-moz-keyframes raise {
  0% {
    -moz-transform: translateY(0px);

  }
  100% {
    -moz-transform: translateY(-10px);
  }
}
@-o-keyframes raise {
  0% {
    -o-transform: translateY(0px);

  }
  100% {
    -o-transform: translateY(-10px);
  }
}
@keyframes raise {
  0% {
    transform: translateY(0px);

  }
  100% {
    transform: translateY(-10px);
  }
}

/* Image Lower */
@-webkit-keyframes lower {
  0% {
    -webkit-transform: translateY(-10px);

  }
  100% {
    -webkit-transform: translateY(0px);
  }
}
@-moz-keyframes lower {
  0% {
    -moz-transform: translateY(-10px);

  }
  100% {
    -moz-transform: translateY(0px);
  }
}
@-o-keyframes lower {
  0% {
    -o-transform: translateY(-10px);

  }
  100% {
    -o-transform: translateY(0px);
  }
}
@keyframes lower {
  0% {
    transform: translateY(-10px);

  }
  100% {
    transform: translateY(0px);
  }
}